import slide from 'components/slide/bottom'

export default {
  name: "index",
  components: {
    slide,
  },
  /*props: {
    dataList: {
      type: Array,
      default: function () {
        // return [];//return {}
        return []
      }
    },
    label: {
      type: String,
      default: 'label'
    },
    value: {
      type: String,
      default: 'value'
    },
    placeholder:{
      type:String,
      default:''
    },
    select:{
      type:String,
      default:''
    }
  },*/
  props:['dataList','label','value','placeholder','select'],
  data() {
    return {
      selected: '',
      selectedLabel: '',
      showPop: false
    }
  },
  methods: {
    openPop(){
      this.showPop = true
    },
    selectedItem(item) {
      // console.log(item)
      if (item.selected === this.selected) return
      this.selected = item[this.value]
      this.selectedLabel = item[this.label]
      this.showPop = false
      this.$emit('changeSel', item)
    },
    setDefault(){
      if (this.select&&this.dataList.length){
        let idx = this.dataList.findIndex((item)=>{
          return this.select===item[this.value]
        })
        this.selected = this.select
        this.selectedLabel = this.dataList[idx][this.label]
      }
    }
  },
  computed: {
  },
  created(){
    /*console.log(this.select)
    if (this.select){
      let idx = this.dataList.findIndex((item)=>{
        return this.select===item[this.value]
      })
      this.selected = this.select
      this.selectedLabel = this.dataList[idx][this.label]
    }*/
  },
  watch:{
    select(v){
      this.setDefault()
    },
    dataList(v){
      this.setDefault()
    }
  }
}



